.Nav {
    width: 100%;
    height: 12vh;
    position: fixed;
    bottom: 0;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .Nav .node {
    font-size: 1.1rem;
    font-weight: 600;
    color: #ffffff;
    width: 5rem;
    height: 5rem;
    border: 2px solid #ffffff;
    border-radius: 50%;
    margin: 0 0.8rem;
    background-color: #333;
  
    display: flex;
    justify-content: center;
    align-items: center;
    transform: scale(0.8);
    transition: 300ms ease-in-out;
  }
  
  .Nav a:hover > .node {
    transform: scale(1);
  }
  
